import React, { useState, useEffect, createContext } from "react";
import Navbar from "../navbar/Navbar";
import Toggle from "../togle/Toggle";
import logoDevice from "../../images/19154.jpg";
import logoDelete from "../../images/delete-icon29.png";
import { api } from "../../api";
import Axios from "axios";
import Swal from "sweetalert2";

import { useHistory } from "react-router";
// import InputTel, { setTextTel } from "./InputTel";

const dataTel = [
  {
    id: "1",
    tel: "0886584811",
  },
  {
    id: "2",
    tel: "0886584822",
  },
  {
    id: "3",
    tel: "0886584833",
  },
  {
    id: "4",
    tel: "0886584844",
  },
];

const inputPhone = createContext();
const toggleSW = createContext();
function DashbordUnlock({ stateL, wifiName, wifiPass, lineToken }) {
  const checkLock = stateL;
  // const checkLock = true;
  const history = useHistory();

  const [dataDevice, setDataDevice] = useState([]);
  const [updatePhone, setUpdatePhone] = useState();
  const [swtogle, setSwTogle] = useState();
  const [dataDevice3, setDataDevice3] = useState([]);

  // var tel = [];

  useEffect(async () => {
    await Axios.get(api + "alldevice3").then((res) => {
      setDataDevice(res.data.data);
    });
    getTel();
  }, []);

  const getTel = async () => {
    await Axios.get(api + "device3").then((res) => {
      setDataDevice3(res.data.data);
    });
  };

  const lock = () => {
    Swal.fire({
      position: "center",
      icon: "info",
      title: "กรุณาปลดล็อค",
      showConfirmButton: false,
      timer: 2000,
    });
  };

  const insertDevice = async (t, i) => {
    await Axios.post(api + "device3", {
      device3_tel: t,
      device3_amount: i,
    }).then((res) => {
      const error = res.data.err;
      if (error != false) {
        // console.log("post = fail!!");
      } else {
        // console.log("post = success!!");
      }
    });
  };

  const fechDeleteDevice = async (bath) => {
    // console.log(api + "device3/" + bath);
    await Axios.delete(api + "device3/" + bath);
    // await Axios.delete(api + "clearalldevice2/" + bath);
  };

  const deleteDevice = (bath) => {
    // console.log(bath);
    if (checkLock == false) {
      lock();
    } else {
      Swal.fire({
        title: "ลบ Device",
        text: "ยืนยันที่จะลบ ข้อมูลทั้งหมดของ Device นี้",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "ยืนยัน",
        cancelButtonText: "ยกเลิก",
      }).then((result) => {
        if (result.isConfirmed) {
          fechDeleteDevice(bath);
          history.push("wait");
          Swal.fire({
            position: "center",
            icon: "success",
            title: "ลบข้อมูลสำเร็จ",
            showConfirmButton: false,
            timer: 1000,
          }).then(() => {
            history.push("/");
          });
        }
      });
    }
  };

  const save = async () => {
    // console.log("dataDevice3 = ");
    // console.log(dataDevice3);
    // console.log(dataDevice);
    const { value: formValues } = await Swal.fire({
      title: "เพิ่มเบอร์โทรศัพท์",
      html:
        `<input type="text" id="swal-input1" class="form-control" />`,
      focusConfirm: false,
      preConfirm: () => {
        return [document.getElementById("swal-input1").value];
      },
    });
    if (formValues) {
      const telValue = formValues[0];
      // console.log(telValue.length);
      if (telValue.length <= 1) {
        Swal.fire({
          position: "center",
          icon: "error",
          title: "กรุณากรอกเบอร์โทรสัพ",
          showConfirmButton: false,
          timer: 2000,
        }).then(() => {
          history.push("/");
        });
      } else {
        Swal.fire({
          title: "Add Device",
          text: "เพิ่มราคาของ Device",
          input: "text",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          cancelButtonText: "ยกเลิก",
          confirmButtonText: "บันทึก",
        }).then((result) => {
          if (result.value) {
            // var statustel = false;
            var statusiprice = false;
            const inputPrice = result.value;
            // Axios.get(api + "device3/" + telValue);

            for (let i = 0; i < dataDevice.length; i++) {
              if (
                telValue == dataDevice[i].device3_tel &&
                inputPrice == dataDevice[i].device3_amount
              ) {
                console.log("failllll!!!!!!");
                Swal.fire({
                  position: "center",
                  icon: "error",
                  title: "ราคาซ้ำ",
                  text: "ไม่สามารถเพิ่มอุปกรณ์ได้",
                  showConfirmButton: false,
                  timer: 1500,
                });
                return ;
              } else {
                statusiprice = true;
              }
            }
            if (statusiprice == true) {
              console.log("success!!!!!!");
              insertDevice(telValue, inputPrice);
              history.push("wait");
              Swal.fire({
                position: "center",
                icon: "success",
                title: "เพิ่มข้อมูลสำเร็จ",
                showConfirmButton: false,
                timer: 1500,
              }).then(() => {
                history.push("/");
              });
            }
          }
        });
      }
    }
  };

  return (
    <div>
      <div className="box-dashbord">
        <div className="row ">
          <toggleSW.Provider value={{ swtogle, setSwTogle }}>
            {dataDevice.map((index, key) => {
              return (
                <div className="col-md-3 " key={key}>
                  <div className="crad-item">
                    <img
                      className="logo-delete"
                      src={logoDelete}
                      onClick={() => {
                        deleteDevice(index.device3_id);
                      }}
                    />
                    <img className="logo-device" src={logoDevice} />
                    <div>
                      <h4>อุปกรณ์ที่ : {key + 1}</h4>
                      {/* <inputPhone.Provider value={{ textPhone, setTextPhone }}>
                      <InputTel id={index.device_id} tel={index.device_tel} />
                    </inputPhone.Provider> */}
                      <h5 className="hh5">Tel : {index.device3_tel}</h5>
                      <div className="d-flex box-price">
                        <h5>ราคา : </h5>

                        <input
                          className="input-price form-control"
                          type="text"
                          defaultValue={index.device3_amount}
                          onChange={(e) => setUpdatePhone(e.target.value)}
                          maxlength={10}
                          size="10"
                          disabled
                        />
                      </div>

                      <Toggle
                        checkstate={checkLock}
                        deviceTel={index.device3_tel}
                        deviceIDs={index.device3_id}
                        deviceBath={index.device3_amount}
                        statusDevices={index.device3_onoff}
                      />
                    </div>
                  </div>
                </div>
              );
            })}
          </toggleSW.Provider>

          <div className="col-md-3 ">
            <div className="crad-item">
              <i
                className="bi bi-plus-square-fill icons"
                onClick={() => save()}
              />
              <p className="add-device">Add Device</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
export { toggleSW };
export { inputPhone };
export default DashbordUnlock;
